.header {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid black;
  align-items: center;
  position: relative;
  width: 450px;
  max-width: 98vw;
  padding-bottom: 0px;
  padding-top: 2px;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}
.headertind {
  height: 35px;
  width: 35px;
  /* padding: 40px; */
  object-fit: contain;
}
.headericon {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 1px;
}
