.analytics {
  width: 450px;
  max-width: 95vw;
  display: flex;
  justify-content: space-evenly;
  
}
.like{
  color: #ffffff;
}
.dislike{
  color: #ffffff;
}
.whatsapp{
  color: #ffffff;
}
.feedback{
  color: #ffffff;
}
.cardlogin {
  position: relative;
  width: 450px;
  padding: 5px;
  max-width: 95vw;
  height: 80vh;
  border-radius: 20px;
  background-size: cover;
  background-position: center;
  box-shadow: 0px 18px 53px 0px rgba(0, 0, 0, 0.3);
  background-color: #000000;
}
.card > h3 {
  position: absolute;
  bottom: 10px;
  color: black;
}
.tinderCards_cardContainer {
  display: flex;
  justify-content: center;
  margin-top: 0vh;
}

.swipe {
  position: absolute;
}

.buttonn {
  background-color: #09acf8; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 2vh;
  margin: 4px 2px;
  cursor: pointer;
  padding: 14px 40px;
  border-radius: 20px;
  align-items: center;
  margin-top: 2vh;
}
.share {
  background-color: #05912f; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 2vh;
  margin: 4px 2px;
  cursor: pointer;
  padding: 14px 53px;
  border-radius: 20px;
  align-items: center;
  margin-top: 2vh;
}
.img {
  align-items: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.container {
  padding: 16px;
}

img.avatar {
  height: 100px;
  border-radius: 50%;
}

.imgcontainer {
  margin-top: 2vh;
  text-align: center;
}

.button {
  background-color: DodgerBlue;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  margin-top: 1vh;
  display: flex;
  justify-content: space-evenly;
  font-weight: 200;
}
.signintext {
  color: rgb(250, 251, 253);
  padding-top: 2vh;
  font-family: Arial;
  text-align: center;
  font-size: large;
  margin: 0 0 5px;
  font-weight: 800;
}
.signedintext {
  color: DodgerBlue;
  padding-top: 1vh;
  font-family: Arial;
  text-align: center;
  font-size: 2.2vh;
  margin: 0 0 5px;
  font-weight: 600;
}
.name {
  color: whitesmoke;
  padding-top: 2vh;
  font-family: Arial;
  text-align: center;
  font-size: 4vh;
  margin: 0 0 5px;
  font-weight: 600;
}
.email {
  color: rgb(248, 242, 242);
  font-family: Arial;
  text-align: center;
  font-size: 3vh;
  margin-top: 2vh;
  margin-left: 20px;
  margin-right: 20px;
}

.btn {
  position: relative;
}

.swipeButtons_fav {
  color: #ff473e !important;
}
.swipeButtons_rep {
  color: #039200 !important;
}
.swipeButtons_canc {
  color: #0585fd !important;
}
.swipeButtons_loc {
  color: brown !important;
}
.swipeButtons_what {
  color: #5061fb !important;
}
.swipeButtons_web {
  color: #f5cf00 !important;
}
